import React,{Component} from 'react'
import Card3 from '../../Components/article/article3'
import classes from './SearchPage.module.css'
import axios from 'axios'
import Heading from '../../Components/Utility_Funtions/Heading/Heading'
import SortBy from '../../Components/Utility_Funtions/Sort-by/SortBy'

class SearchPage extends Component {
    state ={
        articles:[],
        currentCategory:null,
        isLoading:true,
        CurrentSortBy:"popularity"
    }
    sortBy=this.state.CurrentSortBy;
    search=this.props.location.search.substring(8);
    sortBy_handler(){
        let SortByElement=document.querySelector(".SortBy");
        this.sortBy=SortByElement.value
        this.componentDidMount();
    }
    componentDidMount(){
        this.loadData();   
    }
    componentDidUpdate(){
        this.search=this.props.location.search.substring(8);
        this.loadData();
    }
     loadData(){
        this.url=`https://newsapi.org/v2/everything?q=${this.search}&sortBy=${this.sortBy}&apiKey=f8dfe866d1324b099e89197900007ad1`
         if(this.state.isLoading===true || 
            this.state.currentSearch !== this.search ||
            this.state.CurrentSortBy !==this.sortBy  )
         axios.get(this.url)
         .then(response => {
                    this.setState({articles:response.data.articles,
                        isLoading:false,
                        currentSearch:this.search,
                        CurrentSortBy:this.sortBy
                    })
                console.log("error");
                
                })
    }
        header="";
        article="";
    render() { 
        
        if(!this.state.isLoading){
    this.article= this.state.articles.map( (art,i) => {
            return(
                <Card3 article={art}
                key={this.search+i} search={this.search}
                />
            )
        } )
    }
        return ( <React.Fragment>  
                <div className={classes.Search}>
                <Heading
                    title={`Showing Search Results for "${this.search}"`}>
                    <SortBy click={()=>this.sortBy_handler()}/>
                </Heading>
                    {this.article}
                </div>
        </React.Fragment> );
    }
}
 
export default SearchPage;
